import React from "react"
import {graphql} from "gatsby"
import Layout from "../elements/layout";
import Header from "../elements/header/header";
import Container from "../elements/container/container";
import Blogcontent from "../elements/blogcontent/blogcontent";
import IllustrationBackgroundModern from "../background/background_3/backgroundmodern";
import Person from "../elements/person/contactblock";
import SEO from "../elements/seo";
import "./style.css";
import CTA from "../elements/cta/cta";
import rehypeReact from 'rehype-react';
import Audio from "../elements/audio/audio";
import Button from "../elements/button/button";
import HeaderImage from "../elements/headerimg/headerimage";
import FancyBox from "../elements/fancy/fancy";
import Thumbnail from "../elements/thumbnail/thumbnail";
import Image from "../elements/image/image";

export default function Template({
                                     data, // this prop will be injected by the GraphQL query below.
                                 }) {
    const {markdownRemark} = data;// data.markdownRemark holds our post data
    const {frontmatter, htmlAst} = markdownRemark;
    console.log(data)
    return <Layout>

        <SEO title={frontmatter.title}
             keywords={'Umsatz, Videomarketing'}
             description={frontmatter.subtitle}/>

        <Header title={frontmatter.title}
                subtitle={frontmatter.subtitle}
                button={{href: '', text: 'Jetzt lesen'}}>

            <HeaderImage><Thumbnail projekt={frontmatter.projekt}/></HeaderImage>
        </Header>


        <IllustrationBackgroundModern style={{'backgroundColor': 'white'}}>

            <Container>
                <Blogcontent>
                    {htmlAst.children.length >= 1 && <div className={"blogcontent"}>


                        <div>
                            <div className="blog-post-content">{renderAst(htmlAst)}</div>
                        </div>

                        <div className={"rightside"}>
                            <FancyBox>
                                <Person reverse={true}
                                        person={require('./../' + frontmatter.author + '.json')}
                                        style={{
                                            'position': 'sticky',
                                            'top': '20px'
                                        }}>
                                    <Button>Kontaktieren</Button>
                                </Person>
                            </FancyBox>

                        </div>


                    </div>
                    }
                </Blogcontent>

            </Container>

            <br/>

            <CTA/>

        </IllustrationBackgroundModern>
    </Layout>;
}

const renderAst = new rehypeReact({
    createElement: React.createElement,
    components: {"audiofile": Audio, "localimage": Image},
}).Compiler;

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      htmlAst
      frontmatter {
        subtitle
        title
        path
        title
        projekt
        author
      }
    }
  }
`;